import { createSlice } from '@reduxjs/toolkit'

let initialState = [
	{
		title: '',
		isToggle: false,
		base: 'hr',
		children: [
			{
				title: '',
				hashRoute: 'staff',
				data: '',
			},
			{
				title: '',
				hashRoute: 'attendance',
				data: '',
			},
		],
	},
	{
		title: '',
		isToggle: false,
		base: 'purchase',
		children: [{ title: '', hashRoute: 'purchaseSheet', data: '' }],
	},
	{
		title: '',
		isToggle: false,
		children: [
			{ title: '', hashRoute: 'employee', data: '' },
			{ title: '', hashRoute: 'employee', data: '' },
		],
	},
	{ title: '', isToggle: false, children: [{ title: '' }] },
	{
		title: '',
		isToggle: false,
		children: [{ title: '' }],
	},
	{
		title: '',
		isToggle: false,
		children: [{ title: '' }],
	},
	{
		title: '',
		isToggle: false,
		children: [{ title: '' }, { title: '' }],
	},
	{
		title: '',
		isToggle: false,
		children: [{ title: '' }, { title: '' }],
	},
	{
		title: '',
		isToggle: false,
		base: 'other',
		children: [
			{
				title: '',
				hashRoute: 'auth',
			},
			{
				title: '',
				hashRoute: 'category',
			},
			{
				title: '',
				hashRoute: 'size',
			},
			{
				title: '',
				hashRoute: 'color',
			},
			{
				title: '',
				hashRoute: 'condition',
			},
			{
				title: '',
				hashRoute: 'brand',
			},
			{
				title: '',
				hashRoute: 'position',
			},
		],
	},
]

export const sidebarSlice = createSlice({
	name: 'sidebar',
	initialState: initialState,
	reducers: {
		toggle: (state, action) => {
			state.map((item) =>
				item.title === action.payload
					? (item.isToggle = true)
					: (item.isToggle = false)
			)
		},
		setRoute: (state, action) => {
			console.log(action)
			state.forEach((items) => {
				items.children.forEach((item) => {
					if (action.payload === item.title) {
						console.log('admin/' + items.base + '/' + item.hashRoute)
						window.location.hash = 'admin/' + items.base + '/' + item.hashRoute
					}
				})
			})
		},
	},
})

export const { toggle, setRoute } = sidebarSlice.actions

export default sidebarSlice.reducer
